:root {
  --font: "Baloo Tamma 2", cursive;
  --main-colour: #8c4bb4;
  --secondary-colour: #b48c4b;
  --ternary-colour: #4bb48c;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font);
}

body {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1,
h2,
p {
  color: var(--main-colour);
}

.container {
  background-color: var(--secondary-colour);
  height: 80vh;
  width: 60vw;
  border-radius: 2rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 1fr);
}